<template>
  <div id="app" ref="app" />
</template>

<script>
import * as THREE from 'three'
import OrbitControls from 'three-orbitcontrols'
import { MTLLoader,OBJLoader } from 'three-obj-mtl-loader'
// import { OBJLoader } from 'three-obj-mtl-loader'
export default {
    name: 'app',
    data() {
        return {
            scene:null,
            camera:null,
            renderer:null,
            controls:null,
            publicPath: process.env.BASE_URL // 静态资源地址
        }
    },
    methods:{
        initTHREE() {
            // 场景
            this.scene = new THREE.Scene();
            this.scene.add(); 

            // 点灯光照射点
            var target = new THREE.Object3D();
            target.position.set(0,0,0);
            this.scene.add(target);

            // 环境灯光
            var ambientLight = new THREE.AmbientLight(0xffffff, 0.3 )
            this.scene.add(ambientLight);

            // 点灯光
            var pointLight = new THREE.PointLight( 0xffffff);
            pointLight.intensity = 0.3
            pointLight.angle = Math.PI/2;
			pointLight.position.set( 12, 6, 0  );
            pointLight.castShadow = true;
            pointLight.shadow.camera.near = 2;
            pointLight.target = target;
            this.scene.add( pointLight );

            // var helper1  = new THREE.CameraHelper(pointLight.shadow.camera);
            // this.scene.add(helper1);

            // 平行灯光
            var directionalLight = new THREE.DirectionalLight( 0xffffff, 0.3 );
            directionalLight.position.set( 9, 8, 0 );
            directionalLight.shadow.camera.left = -20;
            directionalLight.shadow.camera.right = 20;
			directionalLight.castShadow = true;
            this.scene.add( directionalLight );

            // var helper2  = new THREE.CameraHelper(directionalLight.shadow.camera);
            // this.scene.add(helper2);

            // 相机
            this.camera = new THREE.PerspectiveCamera( 60, window.innerWidth / window.innerHeight, 1, 1000 ); // 视野角度 长宽比 
            this.camera.position.set( 15, 10, 10 );  // x ,y ,z

            // 场景
            this.renderer = new THREE.WebGLRenderer();
            this.renderer.setSize( window.innerWidth, window.innerHeight );
            this.renderer.shadowMap.enabled = true;

            // 场景的颜色
            this.renderer.setClearColor('rgb(135,206,250)',1.0);

            this.$refs.app.appendChild( this.renderer.domElement );
            
            // 控制
            this.controls = new OrbitControls( this.camera, this.renderer.domElement )
            this.controls.update();

            // var object = new THREE.Mesh(new THREE.BoxBufferGeometry( 1, 1, 1 ),new THREE.MeshLambertMaterial({ color: 0xFF1493 }))
            // this.scene.add( object )

            // var object1 = new THREE.Mesh(new THREE.BoxBufferGeometry( 1, 1, 1 ),new THREE.MeshLambertMaterial({ color: 0x6A5ACD }))
            // object1.position.x = 8
            // this.scene.add( object1 )

            // var object2 = new THREE.Mesh(new THREE.BoxBufferGeometry( 1, 1, 1 ),new THREE.MeshLambertMaterial({ color: 0x6A5ACD }))
            // object2.position.set(4,10,0)
            // this.scene.add( object2 )
        },
        animate() {
            requestAnimationFrame( this.animate );
            this.renderer.render( this.scene, this.camera );
        },
        initModel() {
            var _that = this
            new MTLLoader().setPath(`${_that.publicPath}model/`).load(`bdrc.mtl`,materials=>{
                materials.preload();
                new OBJLoader()
                    .setMaterials(materials)
                    .setPath(`${_that.publicPath}model/`)
                    .load(`bdrc.obj`,(obj)=>{
                        obj.traverse(child=>{
                            if(child instanceof THREE.Mesh) {
                                child.castShadow = true
                                child.receiveShadow = true;
                                // if(child.name === 'Mesh1 Group2 Group1 __23_1 ___5_1 Model'){
                                //     // console.log(213)
                                //     child.material = new THREE.MeshBasicMaterial({color: 0xDC143C});
                                //     // child.material.map = texture;
                                // }
                            }
                        })
                        obj.position.z = 13
                        _that.scene.add(obj);
                    })
            })
        }
    },
    mounted() {
        this.initTHREE();
        this.initModel();
        this.animate();
    }
}
</script>
<style>
  #app {
    margin: 0;
    padding: 0;
    height: 100%;
    overflow: hidden;
  }
  html,body{
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
  }
</style>
